import "./App.css";
import Card from "./Card";
import { useState } from "react";

function App() {
  let products = [
    {
      id: 1,
      image:
        "https://i.pcmag.com/imagery/reviews/046UyKcU86Megq3Eenq0sbo-1.1597063431.fit_lpad.size_500x500.jpg",
      name: "Headphone",
      price: 2199,
      description:
        "The zero-compromise Sony WH-CH510 Stamina Wireless On-Ear Headphones feature an extra-long battery life of up to 35 hours.",
      ratting: 4.5,
    },
    {
      id: 2,
      name: "iphone Case",
      image:
        "https://5.imimg.com/data5/HD/LC/ST/ANDROID-88917737/1569756347434-jpg-500x500.jpg",
      price: 250,
      description:
        "The form of the silicone case fits snugly over the volume buttons, side button and curves of your device without adding bulk.",
      ratting: 3,
    },
    {
      id: 3,
      name: "Ink Pen",
      image:
        "https://4.imimg.com/data4/FM/XW/MY-29376586/parker-pen-500x500.jpg",
      price: 300,
      description:
        "The Parker Pen Company is an American manufacturer of luxury pens, founded in 1888 by George Safford Parker..",
      ratting: 1.5,
    },
    {
      id: 4,
      name: "Thermometer",
      image:
        "https://5.imimg.com/data5/SELLER/Default/2020/11/ZD/JL/CB/52256097/infrad-500x500.jpg",
      price: 1499,
      description:
        "Pediatrician Recommended Hospital Grade Quality Thermometer Must For Every Home And Specially Doctors",
      ratting: 3.6,
    },
    {
      id: 5,
      name: "Mask N-95",
      image:
        "https://5.imimg.com/data5/GV/YT/UW/SELLER-2991505/kam-500x500.PNG",
      price: 30,
      description:
        "Premium quality 3 ply mask with Nose pin and ultrasonic welded ear loop in smart pull dispenser box packing mask from brand Bildos",
      ratting: 4.2,
    },
    {
      id: 6,
      name: "Clock",
      image:
        "https://5.imimg.com/data5/YS/RL/MY-5115571/antique-look-round-12-inch-wall-clock-500x500.jpg",
      price: 399,
      description:
        "Dial Shape: Round, Shape: Round, Type: Analog, Occasion: Festival, Power Source: Battery",
      ratting: 5,
    },
  ];

  const [cart, setCart] = useState([]);
  const [totalPrice, setTotalPrice] = useState(0);
  const [totalItems, setTotalItems] = useState(0);
  const [disable, setDisable] = useState([]);

  let addToCart = (item) => {
    setCart([...cart, item]);
    setTotalPrice(+(totalPrice + item.price).toFixed(2));
    setTotalItems(totalItems + 1);
    setDisable([...disable, item.id]);
  };

  let removeCart = (item) => {
    setCart(cart.filter((obj) => obj.id !== item.id));
    setTotalPrice(+(totalPrice - item.price).toFixed(2));
    setTotalItems(totalItems - 1);
    setDisable(disable.filter((obj) => obj !== item.id));
  };

  return (
    <>
      <div className="container">
        <h2 class="display-4">Store.in</h2>
        <div className="row">
          <div className="col-lg-9">
            <div class="row">
              {products.map((product) => {
                return (
                  <Card
                    productData={product}
                    handleCart={addToCart}
                    disableState={disable}
                  ></Card>
                );
              })}
            </div>
          </div>

          <div class="col-lg-3">
            <h3 class="my-4">
              In your Cart{" "}
              <span class="badge badge-pill badge-secondary">{totalItems}</span>
            </h3>
            <div class="list-group">
              {cart.map((item) => {
                return (
                  <div class="list-group-item d-flex">
                    <span class=" flex-fill">
                      {" "}
                      {item.name} ➜ ₹{item.price}
                    </span>

                    <button
                      className="btn btn-sm btn-danger flex-end"
                      onClick={() => {
                        removeCart(item);
                      }}
                    >
                      X
                    </button>
                  </div>
                );
              })}
            </div>
            <h4 class="text-right mr-4 mt-3">Total - ₹{totalPrice}</h4>
          </div>
        </div>
      </div>
    </>
  );
}

export default App;
