export default function Card(props) {
  let isDisabled = props.disableState.includes(props.productData.id);
  return (
    <>
      <div class="col-lg-4 col-md-6 mb-4">
        <div class="card h-100">
          <a href="#!">
            <img
              class="card-img-top"
              src={`${props.productData.image}`}
              alt="..."
            />
          </a>
          <div class="card-body p-3">
            <h4 class="card-title">
              <a href="#!">{props.productData.name}</a>
            </h4>
            <hr></hr>
            <h5>₹{props.productData.price}</h5>
            <p class="card-text">{props.productData.description}</p>
          </div>
          <div class="card-footer d-flex">
            <h5 class="text-muted flex-fill">
              {calRatting(props.productData.ratting)}
            </h5>
            <span>
              <button
                className="btn btn-sm btn-primary flex-end"
                onClick={() => {
                  props.handleCart(props.productData);
                }}
                disabled={isDisabled}
              >
                Add To Cart
              </button>
            </span>
          </div>
        </div>
      </div>
    </>
  );

  // calculate the ratting even of decimal values (3.6)=> 4
  function calRatting(ratting) {
    let rate = 1;
    rate = Math.round(ratting);
    switch (rate) {
      case 1:
        return (ratting = "★ ☆ ☆ ☆ ☆");

      case 2:
        return (ratting = "★ ★ ☆ ☆ ☆");

      case 3:
        return (ratting = "★ ★ ★ ☆ ☆");

      case 4:
        return (ratting = "★ ★ ★ ★ ☆");

      case 5:
        return (ratting = "★ ★ ★ ★ ★");
      default:
        return (ratting = ""); //no ratting
    }
  }
}
